// ===========HEADER=====================

header {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    .red_header {
        width: 100%;
        height: 64px;
        background: rgb(255, 52, 52);
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 200px;

        h4 {
            font-family: 'Montserrat', sans-serif;
            font-weight: normal;
            font-size: 17px;
            color: white;
        }

        .social {
            margin-left: 30px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            h4 {
                margin-right: 10px;
            }

            .social-media {
                display: flex;
                justify-content: center;
                align-items: center;

                a {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: 0 10px;

                    img {
                        transition: 0.3s;
                    }

                    img:hover {
                        transform: perspective(400px) scaleZ(-1) translateZ(-50px);
                    }
                }

            }
        }
    }

    .header-find {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 200px;

        a {
            margin-bottom: 15px;
        }

        .find {
            width: 50%;

            form {
                width: 100%;
                height: 74px;
                background: #F1F1F1;
                box-shadow: 0px 0px 4px rgba(255, 0, 0, 0.25);
                border-radius: 50px 50px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 0 0 0 35px;

                input {
                    width: 60%;
                    border: none;
                    background: #F1F1F1;
                    height: 74px;
                    font-weight: 700;
                    color: rgba(255, 0, 0, 0.5);
                    font-size: 20px;
                    line-height: 23px;
                    font-family: 'Montserrat', sans-serif;
                }

                input:focus {
                    border: none;
                    outline: none;
                }

                input::placeholder {
                    color: rgba(255, 0, 0, 0.5);
                    font-size: 20px;
                    line-height: 23px;
                    font-family: 'Montserrat', sans-serif;
                }

                button {
                    height: 74px;
                    width: 20%;
                    border: none;
                    border-radius: 50px;
                    background: #F1F1F1;
                    cursor: pointer;
                }

                button:focus {
                    border: none;
                    outline: none;
                    cursor: pointer !important;
                }
            }
        }

        .numbers {
            margin-left: 30px;
            display: flex;
            justify-content: center;
            align-items: center;

            .num_icon {
                display: flex;
                justify-content: center;
                align-items: center;
                background: #EAEAEA;
                height: 74px;
                border-radius: 50px;
                padding-right: 40px;

                .call {
                    margin-right: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 50px;
                    height: 74px;
                    min-width: 74px;
                    max-width: 74px;
                    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
                    animation: call_rotate 2s linear infinite;
                }

                .num {
                    h5 {
                        font-size: 20px;
                        line-height: 23px;
                        font-family: 'Montserrat', sans-serif;
                        color: #FF0000;
                    }
                }
            }
        }
    }

    .mob-find {
        display: none;
        width: 100%;
        padding: 0 10px;
        margin-bottom: 30px;

        form {
            width: 100%;
            height: 50px;
            background: #F1F1F1;
            box-shadow: 0px 0px 4px rgba(255, 0, 0, 0.25);
            // border-radius: 50px 50px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 0 0 35px;

            input {
                width: 60%;
                border: none;
                background: #F1F1F1;
                height: 50px;
                color: rgba(255, 0, 0, 0.5);
                font-size: 20px;
                line-height: 23px;
                font-family: 'Montserrat', sans-serif;
            }

            input:focus {
                border: none;
                outline: none;
            }

            input::placeholder {
                color: rgba(255, 0, 0, 0.5);
                font-size: 20px;
                line-height: 23px;
                font-family: 'Montserrat', sans-serif;
            }

            button {
                height: 50px;
                width: 20%;
                border: none;
                // border-radius: 50px;
                background: #F1F1F1;
                transition: 0.3s;

                img {
                    width: 30px;
                    height: 30px;
                }
            }

            button:hover {
                // transform: perspective(400px) scaleZ(-1) translateZ(-70px);
                box-shadow: 0 0 15px 5px rgb(180, 180, 180);
            }

            button:focus {
                border: none;
                outline: none;
            }
        }
    }
}

