// ==================SLIDER=====================
// ==================SLIDER=====================
// ==================SLIDER=====================

.slider {
    .container-fluid {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.swiper-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    .swiper-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .swiper-slide {
            overflow: visible;
            max-height: 560px;
            min-height: 560px;
            position: relative;
            z-index: 3;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 25px 80px;
            background: #FFFFFF;
            border: 1px solid #CACACA;
            box-sizing: border-box;
            // box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);

            .slide-img {
                img {
                    max-width: 508px;
                    max-height: 508px;
                }
            }

            .slide-text {
                margin-left: 75px;
                display: flex;
                justify-content: space-around;
                align-items: flex-start;
                flex-direction: column;
                font-family: 'Montserrat', sans-serif;

                h4 {
                    font-style: normal;
                    font-weight: 700;
                    font-size: 35px;
                    line-height: 43px;
                    color: #000000;
                }

                p {
                    margin-top: 20px;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 30px;
                    color: #000000;
                }

                a {
                    box-shadow: 0px 5px 7px rgba(0, 0, 0, 0.25);
                    margin-top: 20px;
                    width: 246px;
                    height: 59px;
                    background: red;
                    font-style: normal;
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 30px;
                    color: #FFFFFF;
                    text-decoration: none;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: 0.3s;
                }

                a:hover {
                    box-shadow: 0px 0px 0px 2px rgb(0, 0, 0);
                }
            }

        }

    }
}

.slider {
    padding: 0 200px;
}

.swiper-button-prev {
    top: 50%;
    left: -10px;
    position: absolute;
    width: 54px;
    height: 54px;
    background: #FFFFFF;
    border: 1px solid #C0C0C0;
    border-radius: 50%;
    box-sizing: border-box;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
    padding-right: 3px;

    img {
        width: 15px;
        height: 25px;

    }
}

.swiper-button-next {
    top: 50%;
    right: -10px;
    position: absolute;
    width: 54px;
    height: 54px;
    background: #FFFFFF;
    border: 1px solid #C0C0C0;
    border-radius: 50%;
    box-sizing: border-box;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
    padding-left: 3px;

    img {
        width: 15px;
        height: 25px;

    }
}

.swiper-button-prev:focus,
.swiper-button-next:focus {
    outline: none;
}

