    }
}

// ===================TOVARI====================
// ===================TOVARI====================
// ===================TOVARI====================

.tovari {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 200px;

    .container-fluid {
        .tovari_line {
            margin-top: 20px;
            width: 100%;
            padding: 0 3px;

            .line_word {
                width: 205px;
                height: 46px;
                background: red;
                display: flex;
                justify-content: center;
                align-items: center;
                font-style: normal;
                font-weight: normal;
                font-size: 30px;
                line-height: 49px;
                font-family: 'Montserrat', sans-serif;
                padding-top: 3px;
                color: white;
                // border-radius: 10px 10px 0 0;
            }

            .line_line {
                width: 100%;
                height: 5px;
                background: red;
            }
        }

        .tovari_div {
            width: 100%;
            margin-top: 0px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;

            .item {
                margin: 0 3px;
                margin-top: 20px;
                max-width: 288px;
                min-width: 288px;
                height: 450px;
                border: 1px solid #000000;
                box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
                text-align: center;
                font-family: 'Montserrat', sans-serif;
                color: black;

                .item_img {
                    padding: 18px;
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }

                p {
                    padding: 0 10px;
                    font-size: 17px;
                    line-height: 24px;
                    font-weight: 700;
                    margin-bottom: 6px;
                }

                h4 {
                    padding: 0 10px;
                    font-size: 17px;
                    line-height: 24px;
                    font-weight: 600;
                    animation: margin_h4 linear 5s infinite;
                }

                .item_buttons {
                    margin-top: 15px;
                    margin-bottom: 5px;
                    width: 100%;
                    height: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    a {
                        text-align: center;
                        width: 100% !important;
                        height: 40px !important;
                        background: red;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        color: white;
                        font-size: 20px;
                        line-height: 29px;
                        font-weight: 500;
                        text-decoration: none;
                        transition: 0.5s;
                    }

                }

                .item_buttons:hover a {
                    background: rgb(0, 48, 179);

                }

                .dropdown_div {
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    padding-left: 10px;
                    padding-right: 10px;
                    padding-top: 7px;
                    padding-bottom: 7px;
                    align-items: center;
                    background: rgba(255, 255, 255, 0);
                    flex-wrap: nowrap;

                    form {
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;

                        h5 {
                            font-size: 15px;
                            margin-left: 5px;
                            margin-right: 5px;
                            font-weight: 400;
                        }
                    }

                    .add,
                    .take {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 20px;
                        height: 20px;
                    }

                    select {
                        width: 55px;
                        height: 25px;
                        border: 1px solid #000000;
                        outline: none;
                        font-weight: 600;
                        background: rgb(255, 255, 255);
                        color: rgb(0, 0, 0);
                        padding: 0 !important;
                        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);

                        option {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            text-align: center;
                        }
                    }

                    select:focus {
                        outline: none;
                    }

                    input {
                        width: 73%;
                        height: 30px;
                        border: none;
                        outline: none;
                        transition: 0.5s;
                        background: #0039b4;
                        background-repeat: no-repeat;
                        color: white;
                    }

                    input:hover {
                        background: rgb(0, 0, 0);
                    }

                    input:focus {
                        border: none;
                        outline: none;
                    }
                }
            }
        }
    }
}

