.tovar {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 200px;

    .container-fluid {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;

        .tovar_left {
            height: 100%;
            padding: 40px 0 0 0;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            .tovar_img {
                width: 415px;
                height: 415px;
                background: #ffffff;
                -webkit-box-sizing: border-box;
                box-sizing: border-box;
                filter: drop-shadow(0px 0px 10px #FF0000);
                box-shadow: -10px -10px 0px 1px #ff0000;
                padding: 20px;
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    width: 400px;
                    height: 400px;
                }
            }
        }

        .tovar_right {
            padding: 20px;
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: center;
            -webkit-justify-content: center;
            -ms-flex-pack: center;
            justify-content: center;
            -webkit-box-align: start;
            -webkit-align-items: flex-start;
            -ms-flex-align: start;
            align-items: flex-start;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -webkit-flex-direction: column;
            -ms-flex-direction: column;
            flex-direction: column;
            text-align: left;

            h1 {
                font-size: 35px;
                line-height: 42px;
                font-weight: 600;
                font-family: 'Montserrat', sans-serif;
                padding-bottom: 3px;
                border-bottom: 2px #ff0000 solid;
                margin-bottom: 20px;
                background: -webkit-gradient(linear, left top, left bottom, from(#000a97), to(#0099ff));
                background: -o-linear-gradient(top, #000a97, #0099ff);
                background: linear-gradient(to bottom, #000000, #7c7c7c);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                text-shadow: 0px 7px 4px rgba(0, 0, 0, 0.25);
