                    width: 80px;
                    height: 35px;
                    border: none;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 17px;
                    padding-left: 10px;
                    text-align: center;
                    // border-radius: 50px;
                    font-weight: bold;
                    color: rgb(0, 0, 0);
                    background: #ffffff;
                    -webkit-box-shadow: 0px 0px 4px 4px rgba(0, 0, 0, 0.25);
                    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
                    margin-right: 10px;
                }

                option {
                    display: -webkit-box;
                    display: -webkit-flex;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-pack: center;
                    -webkit-justify-content: center;
                    -ms-flex-pack: center;
                    justify-content: center;
                    -webkit-box-align: center;
                    -webkit-align-items: center;
                    -ms-flex-align: center;
                    align-items: center;
                    width: 80px;
                    font-weight: 600;
                    text-align: center;
