                }

                .korzina_form {
                    position: absolute;
                    bottom: 10px;
                    right: 5px;
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;

                    form {
                        display: flex;
                        flex-wrap: wrap;

                        label {
                            font-size: 17px;
                            font-family: 'Montserrat', sans-serif;
                            font-weight: 600;
                            text-shadow: 0px 7px 4px rgba(0, 0, 0, 0.25);
                            margin-right: 10px;
                            margin-top: 2px;
                        }

                        select {
                            width: 60px;
                            height: 30px;
                            border: none;
                            font-family: 'Montserrat', sans-serif;
                            font-size: 17px;
                            padding-left: 10px;
                            text-align: center;
                            // border-radius: 50px;
                            font-weight: bold;
                            color: rgb(0, 0, 0);
                            background: #ffffff;
                            -webkit-box-shadow: 0px 0px 4px 4px rgba(0, 0, 0, 0.25);
                            box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
                            margin-right: 10px;

                            option {
                                display: -webkit-box;
                                display: -webkit-flex;
                                display: -ms-flexbox;
                                display: flex;
                                -webkit-box-pack: center;
                                -webkit-justify-content: center;
                                -ms-flex-pack: center;
                                justify-content: center;
                                -webkit-box-align: center;
                                -webkit-align-items: center;
                                -ms-flex-align: center;
                                align-items: center;
                                width: 80px;
                                font-weight: 600;
                                text-align: center;
                                height: 50px;
                            }
                        }

                        select:focus {
                            outline: none;
                        }

                        input {
                            width: 100px;
                            height: 30px;
                            border: none;
                            background: rgb(255, 255, 255);
                            transition: 0.2s;
                            color: rgb(0, 0, 0);
                            box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
                            font-family: 'Montserrat', sans-serif;
                            font-size: 14px;
                            font-weight: 700;
                            margin-right: 10px;
                        }

                        input:hover {
                            text-decoration: none;
                            -webkit-box-shadow: 0px 0px 0px 4px #ffae00;
                            box-shadow: 0px 0px 0px 2px #000000;
                        }

                        input:focus {
                            border: none;
                            outline: none;
                        }

                        a {
                            padding: 6px;
