// ===================KEYFRAMES==================
// ===================KEYFRAMES==================
// ===================KEYFRAMES==================

@keyframes margin_h4 {
    0% {
        margin: 0;
    }

    25% {
        margin-left: 20px;
    }

    50% {
        margin: 0;
    }

    75% {
        margin-right: 20px;
    }

    100% {
        margin: 0;
    }
}

@keyframes call_rotate {
    0% {
        transform: rotate(0deg);
    }

    5% {
        transform: rotate(20deg);
    }

    10% {
        transform: rotate(0deg);
    }

    20% {
        transform: rotate(20deg);
    }

    30% {
        transform: rotate(0deg);
    }

    40% {
        transform: rotate(20deg);
    }

    50% {
        transform: rotate(0deg);
    }

    100% {}
}

@keyframes print {
    0% {
        color: rgba(255, 255, 255);
    }

    50% {
        color: rgba(255, 255, 255, 0);
    }

    100% {
        color: rgba(255, 255, 255);
    }
}

